* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}


h2 {
    font-size: 20px;
    font-weight: 200;
    font-family: 'Karla';
}


.service-preview__main-container {
    background-color: #D9D9D9;
    width: 100%;
    min-height: 120%;
    
    display: inline-block;
    overflow-x: hidden;
    z-index: 100;
}
.service-preview__container {
    display: block;
    text-align: center;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}
.num {
    margin-top: 50px;
    font-family: 'Karla';
    font-size: calc(120px + 8 * (100vw - 320px) / 880);
    color:white;
    letter-spacing: 10px;
}
.name {
    margin-top:-40px ;
    margin-bottom: 20px;
}
.img4 {

    background-size: cover;
    background-position: center;
    height: 50%;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
}
.slogan {
    text-decoration: underline;
    margin-bottom: 20px;
    width: 70%;
    margin-left: auto;
    margin-right: auto;

}
.description {
    font-weight: 100;
    font-size: small;
    width: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}
.contact-us{
    width: 300px;
    height: 52px;
    background-color: #E54424;
    border: 0px solid black;
    color:white;
    font-size: 16px;
    letter-spacing: 2px;
    margin-bottom: 40px;
    
}

@media(max-width: 799px) {
    .service-preview__main-container {
        width: 100%;
        display: flex;
        justify-content: center;

        
    }

}

